<template>
  <div>
        <el-form :inline="true" :model="formInline" class="aa demo-form-inline">
            <el-form-item label="请输入需要查询的商品id">
                <el-input v-model="formInline.user" placeholder="商品id"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="onSubmit">查询</el-button>
            </el-form-item>
        </el-form>
       <el-table
        :data="now"
        border
        style="width: 100%">
        <el-table-column
            prop="id"
            label="id">
        </el-table-column>
        <el-table-column
            prop="title"
            label="商品名称">
             <template slot-scope="scope">
                <span v-if="!scope.row.isIptShow" @click="scope.row.isIptShow = !scope.row.isIptShow">
                    {{ scope.row.title }}
                </span>
                <el-input @blur="scope.row.isIptShow = !scope.row.isIptShow" v-else type="text" v-focus.native v-model="scope.row.title"></el-input>
            </template>
        </el-table-column>
        </el-table>
  </div>
</template>

<script>
import { mapState , mapGetters , mapMutations} from "vuex"
import vue from "vue"
export default {
    directives:{
        focus(el){
            vue.nextTick(()=>{
                el.focus();
            })
        }
        // focus:{
        //     inserted(el){
        //         el.focus();
        //     }
        // }
    },
    data(){
        return {
             formInline: {
                user: '',
                region: ''
            },
            tableData: [{
            date: '2016-05-02',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1518 弄'
          }, {
            date: '2016-05-04',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1517 弄'
          }, {
            date: '2016-05-01',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1519 弄'
          }, {
            date: '2016-05-03',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1516 弄'
          }]
        }
    },
    computed:{
        ...mapState({
            arr:"arr"
        }),
        ...mapGetters({
            now:"now"
        })
    },
    destroyed(){
        this.uptSearch("");
    },
    methods:{
        ...mapMutations({
            uptSearch:"uptSearch"
        }),
        onSubmit() {
            this.uptSearch(this.formInline.user)
            console.log('submit!');
        }
    }
}
</script>

<style lang="scss" scoped>
.aa{
    display: flex;
    align-items: center;
    justify-content: flex-start;
}
</style>